html {
	box-sizing: border-box;
	font-size: 100%;
}

*,
*:before,
*:after {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}

:root {
	--dark-grey: #4e5150;
}

body {
	font-family: "Montserrat", sans-serif;
  padding: 30px 7px;
	min-width: 350px;
}

.person {
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
	margin-bottom: 12px;
	img {
		width: 102px;
		height: 102px;
		border-radius: 16px;
	}
	h1 {
		color: var(--dark-grey);
		font-weight: 600;
		font-size: 14px;
		line-height: 17px;
		margin: 12px 0 4px;
	}
	p {
		font-weight: 600;
		font-size: 10px;
		line-height: 12px;
		color: #a9a9a9;
	}
}
.social {
	display: flex;
	align-items: center;
	justify-content: center;
}
.social__card {
	background-color: #e5e5e5;
	width: 78px;
	height: 52px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 0 7.5px;
	p {
		font-size: 12px;
		line-height: 18px;
		color: var(--dark-grey);
		hyphens: auto;
	}
	p.grey {
		color: #bdbdbd;
	}
}

.rounded {
	border-radius: 12px;
}

main {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(3, 1fr);
	gap: 13px;
	max-width: 960px;
	margin: 40px auto;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	figure {
		filter: drop-shadow(0px 4px 6px rgba(0, 0, 0, 0.15));
	}
}

.photo1 {
	grid-column: 1 / 2;
	grid-row: 1 / 2;
}
.photo2 {
	grid-column: 2 / 4;
	grid-row: 1 / 2;
}
.photo3 {
	grid-column: 1 / 3;
	grid-row: 2 / 3;
}
.photo4 {
	grid-column: 3 / 4;
	grid-row: 2 / 4;
}
.photo5 {
	grid-column: 2 / 3;
	grid-row: 3 / 4;
}
.photo6 {
	grid-column: 1 / 2;
	grid-row: 3 / 4;
}

footer {
	font-size: 14px;
	line-height: 17px;
	color: #a9a9a9;
	text-align: center;
}

@media screen and (min-width: 600px) {
	.person {
		img {
			width: 156px;
			height: 156px;
		}
		h1 {
			font-size: 24px;
			line-height: 29px;
		}
		p {
			font-size: 14px;
			line-height: 17px;
		}
	}
	.social__card {
		width: 100px;
		height: 67px;
		p {
			font-size: 18px;
			line-height: 26px;
		}
		p.grey {
			font-size: 14px;
			line-height: 17px;
		}
	}
}
